<template>
  <div class="about">
    <el-form @submit.native.prevent="save" label-width="120px">
      <h1>{{ id ? "编辑" : "新建" }}文章</h1>
      <el-form-item label="所属分类">
        <el-select multiple v-model="model.categories" placeholder="请选择">
          <el-option
            v-for="item in categories"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="标题">
        <el-input v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="详情">
        <vue-editor
          id="editor"
          useCustomImageHandler
          @image-added="handleImageAdded"
          v-model="model.body"
        >
        </vue-editor>
      </el-form-item>
      <el-form-item
        ><el-button type="primary" native-type="submit"
          >提交</el-button
        ></el-form-item
      >
    </el-form>
  </div>
</template>

<script>
import {
  createNewList,
  getListIdInfo,
  updateList,
} from "@/api/admin/articles.js";
import { getCategoryList } from "@/api/admin/categories.js";
import { VueEditor } from "vue2-editor";
export default {
  props: {
    id: {
      type: String,
    },
  },
  data() {
    return {
      model: {},
      categories: [],
    };
  },
  methods: {
    // 提交
    async save() {
      if (this.id) {
        await updateList(this.id, this.model);
      } else {
        await createNewList(this.model);
      }
      this.$router.push("/articles/list");
      this.$message({
        type: "success",
        message: "保存成功",
      });
    },
    // 是以编辑的路由跳转进来的就触发该fetch，绑定数据到model上
    async fetch() {
      const { data: res } = await getListIdInfo(this.id);
      this.model = res;
    },
    // 将所有的分类放置在页面的上级分类中
    async fetchCategories() {
      const { data: res } = await getCategoryList();
      this.categories = res;
    },
    // 组件方法
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      var formData = new FormData();
      formData.append("file", file);
      const { data: res } = await this.$http.post(
        this.$http.defaults.baseURL + "upload",
        formData
      );
      Editor.insertEmbed(cursorLocation, "image", res.url);
      resetUploader();
    },
  },
  created() {
    // 如果有ID这个值，那么是edit页面，触发fetch，否则就是认定为create页面
    this.id && this.fetch();
    // 获取到分类options的选项数据
    this.fetchCategories();
  },
  components: {
    VueEditor,
  },
};
</script>

